.App {
  width: auto;
  font-family: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
}

.contain {
  width: 100%;
  border: solid #333;
  border-bottom-right-radius: 1em;
  border-bottom-left-radius: 1em;
  margin: 1em;
}
.container {
  width: auto;
  text-align: center;
  margin: 1em;
}
.cont {
  margin-top: 1em;
}
.cont img {
  width: 100%;
}